import React, { useEffect, useState } from 'react';
import PictureCard from './components/picturecard';
import Images from './pictures/index';

const App = () => {
  const [pictures, setPictures] = useState({
    images: Images,
    clicked: false,
  });
  const [scoreBoard, setScoreBoard] = useState({
    currentScore: 0,
    clicked: [],
    maxScore: 0,
  });

  const handleClick = (e) => {
    let id;
    !(e.target.id) ? id = e.target.parentNode.id : id = e.target.id;
    let arr = [...scoreBoard.clicked];
    let max = scoreBoard.maxScore;
    if (arr.includes(id)) { //checkloss
      alert('You clicked a repeat element, you lose.');
      setScoreBoard({
        currentScore: 0,
        clicked: [],
        maxScore: max,
      });
      return;
    }
    arr.push(id);
    let score = scoreBoard.currentScore;
    score++;
    (score < max) || (max = score);
    setScoreBoard({
      currentScore: score,
      clicked: arr,
      maxScore: max,
    });
    if (arr.length === 9) { //check win
      alert('You win! Congratualations');
      setTimeout(() => {
        setScoreBoard({
          currentScore: 0,
          clicked: [],
          maxScore: score,
        });;
      }, 1000);
      return;
    }
  };

  useEffect((e) => {
    let tempArr = [...pictures.images];
    shuffleArray(tempArr);
    setPictures((prevState) => {
      return { images: tempArr }
    });

    function shuffleArray(array) {
      for (var i = array.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var temp = array[i];
        array[i] = array[j];
        array[j] = temp;
      }
    }
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [scoreBoard]);

  const createUI = () => {
    return pictures.images.map((item, index) => {
      return (
        <div key={index} onClick={handleClick}>
          <PictureCard item={item} index={index} />
        </div>
      );
    });
  };

  return (
    <div>
      Score: {scoreBoard.currentScore}
      <br></br>
      Max Score: {scoreBoard.maxScore}
      {createUI()}
    </div>
  );
};

export default App;